.CheckBox{z-index:0;display:flex;align-items:center;font-size:1rem}.CheckBox:read-only label{position:relative;border-radius:50%;width:1rem;height:1rem;border:1px solid #000;cursor:pointer}.CheckBox:read-only label::before{content:""}.CheckBox input{position:relative;display:none;width:.9rem;height:.9rem}.CheckBox input:checked~label{background-color:#1c32c5;border:none}.CheckBox input:checked~label::before{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);font-size:7px;color:#fff;content:"✓"}.CheckBox span{cursor:pointer;padding-left:.5rem}